body {
  background-color: #f9f9f9;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  font-family: "Noto Sans JP";
  text-align: center;
}

.App-header h1 {
  font-size: 2.5rem;
  font-family:  Helvetica, sans-serif;
  text-align: left;
  padding-left: 20px;
}

.info-point {
  position: relative;
  width: 35%;/* 各エリアの幅 */
  height: 30px; /* 各エリアの高さ */
  font-weight: bold;
  color: #333;
}

/* ドッグラン */
.dogrun-image {
